<template>
    <section>
        <Breadcrumbs :breadcrumbs="breadcrumbs"/>

        <div class="content-container">
            <form
                class="addition-form"
                @submit.prevent="submitHandler"
            >
                <div class="addition-form__row">
                    <label class="addition-form__label">Наименование</label>

                    <input type="text" name="title" placeholder="наименование"
                        v-model="title"
                        :class="'addition-form__input '+ {invalid: $v.title.$dirty && !$v.title.required}"
                    >

                    <span class="addition-form__validate"
                        v-if="$v.title.$dirty && !$v.title.required"
                    >
                        Введите название товара
                    </span>
                </div>

                <div class="addition-form__row">
                    <label class="addition-form__label">Стоимось</label>

                    <input type="text" name="price" placeholder="стоимость"
                        v-model.number="price" 
                        :class="'addition-form__input '+ {invalid: $v.price.$dirty && !$v.price.required}"
                    >

                    <span class="addition-form__validate"
                        v-if="$v.price.$dirty && !$v.price.required"
                    >
                        Введите стоимость товара
                    </span>
                </div>

                <div class="addition-form__row">
                    <label class="addition-form__label">Размеры</label>

                    <div
                        class="addition-form__size"
                        v-for="size of sizes"
                        :key="size"
                    >
                        <input
                            class="addition-form__checkbox"
                            type="checkbox" 
                            v-model="productSizes"           
                            :value="size"
                        >

                        <span class="addition-form__text">{{size}}</span>
                    </div>
                </div>

                <div class="addition-form__row">
                    <input
                        class="addition-form__checkbox"
                        name="status"
                        id="status"
                        type="checkbox"
                        v-model="status" 
                    >

                    <label class="addition-form__label--checkbox" for="status">
                        Отображать товар                                
                    </label>
                </div>

                <button class="std-btn" type="submit">
                    Добавить
                </button>
            </form>
        </div>
    </section>
</template>

<script>
import {required} from 'vuelidate/lib/validators'
import Breadcrumbs from '@/components/admin/layout/Breadcrumbs'

export default {
    components: {Breadcrumbs},
    metaInfo(){
        return {
            title: this.$title('Добавить товар')
        }
    },
    data: () => ({
        breadcrumbs: {
            title: 'Добавить товар',
            parent: 'Список товаров',
            current: 'Добавить товар',
            parentLink: '/admin/products'
        },
        title: '',
        price: '',
        productSizes: [],        
        status: true,
        sizes: ['s', 'm', 'l', 'xl', 'xxl'],
    }),
    validations: {
        title: {required},
        price: {required}
    },
    methods: {
        async submitHandler(){
            if(this.$v.$invalid){
                this.$v.$touch()
                return
            }
            try {                
                await this.$store.dispatch('createProduct', {
                    id: (new Date().valueOf()).toString().slice(6),
                    title: this.title,
                    price: this.price,
                    status: this.status,
                    sizes: this.productSizes
                })
                this.title = ''
                this.price = ''
                this.productSizes = []
                this.$success('Товар успешно добавлен!')
            } catch (error) {
                this.$error(error.message)
                throw error.message
            }
        }
    }
}
</script>